export enum Quartier {
    inconnu = 'Inconnu',

    noisiel = 'Noisiel',
    montmartre = 'Montmartre',
    saint_maur_des_fosses = 'Saint Maur des fosss',
    chatenay_malabry = 'Chtenay-Malabry',
    saint_malo = 'Saint-Malo',
    montesson = 'Montesson',
    palais_royal = 'Palais royal',
    saint_germain_en_laye = 'Saint Germain en Laye',
    bois_de_boulogne = 'Bois de Boulogne',
    versailles = 'Versailles',
    vanves = 'Vanves',
    luxembourg = 'Luxembourg',
    la_defense = 'La Dfense',
    bondy = 'Bondy',
    maisons_laffite = 'Maisons-Laffite',
    genevilliers = 'Genevilliers',
    saint_ouen = 'Saint Ouen',
    grande_crete = 'Grande crte',
    catacombes_de_paris = 'catacombes de Paris',
    saint_denis = 'Saint-Denis',
    argenteuil = 'Argenteuil',
    suresnes = 'Suresnes',

    vladivostok = 'Vladivostok',
}

export function descriptionQuartier(quartier: Quartier): string {
    let texte: string = "Vous vous trouvez dans le quartier <b>" + quartier + "</b>.<br/>";
    switch (quartier) {
        case Quartier.grande_crete:
            texte += "La grande crte est une aberration gologique survenue durant la grande guerre tectonique. "
                + " cette poque les chthoniens ont attaqu lEurope en y provoquant une srie de sismes qui ont eu pour principal effet la cration dune norme chane de montagne entre Paris et Prague. "
                + "Cette crte a une altitude quivalente  celle des alpes mais est encore plus abrupte. <br/>"
                + "Elle est naturellement trs difficile  amnager mais les schweizer lont trouve  leur got et lont transforme de manire si ingnieuse quelle est devenue prospre et trs bien desservie par les chemins de fers, surtout dans la partie proche de la Ville. ";
            break;
        default:
            texte += "Il manque la description de cet endroit : " + quartier;
    }
    return texte;
}

export function imageQuartier(quartier: Quartier): string {
    switch (quartier) {
        case Quartier.grande_crete:
            return "https://raw.githubusercontent.com/gabriellevy/destin-extremis-react/refs/heads/main/images/quartiers/grande_cr%C3%A8te.jpg";

        default:
            return "Il manque l'image de cet endroit : " + quartier;
    }
}
