import {GroupeEvts} from "../../../types/Evt";
import {Perso, Sexe} from "../../../types/perso/Perso";
import {Quartier} from "../../geographie/quartiers";
import {majReputationDansQuartier} from "../../../types/Reputation";
import {getPatronyme} from "../../../fonctions/noms";
import {Coterie} from "../../../types/Coterie";
import {getValeurCompetence, TypeCompetence} from "../../../types/perso/comps/Comps";
import {aLaMaitrise, Maitrise} from "../../maitrise";
import {ResultatTest} from "../../../types/LancerDe";
import {testComp} from "../../../fonctions/des";
import {ajouterViceVal, getValeurVice, TypeVice} from "../../../types/ViceVertu";

export const evts_chatenay_malabry: GroupeEvts = {
    evts: [
        {
            id: "evts_chatenay_malabry1",
            description: (perso: Perso): string => {
                let majReputation: number = 0;
                let texte:string = "Vous tes invit  un grand banquet celte. "
                    + "Le chef de clan " + getPatronyme(Coterie.celtes, Sexe.male) + " a fait les choses en grand. Du vin de la viande, du miel, tout est excellent. <br/>";
                if (perso.coterie === Coterie.celtes) {
                    majReputation += 1;
                    texte += "En tant que celte vous tes bien accueilli et vite  l'aise. <br/>";
                }
                if (getValeurCompetence(perso, TypeCompetence.armeCaC) >= 50) {
                    majReputation += 1;
                    texte += "Votre rputation de bon combattant vous donne droit de diner  la table des guerriers. <br/>";
                }
                if (aLaMaitrise(perso, Maitrise.poesie)) {
                    texte += "Votre talent de pote intresse l'assemble. On vous demande d'interprter un de vos pomes. ";
                    const resTestDiscours:ResultatTest = testComp(perso, {comp: TypeCompetence.eloquence, bonusMalus: 0});
                    texte += resTestDiscours.resume;
                    if (resTestDiscours.reussi) {
                        texte += "Votre interprtation impressionne le roi et tous les convives. Nul doute que votre rputation va augmenter. <br/>";
                        majReputation += 3;
                    } else {
                        if (resTestDiscours.critical) {
                            texte += "Peut-tre est-ce le vin pur, peut-tre est-ce le trac, mais vous faites erreurs sur erreur et faute de got sur faute de got. Vous interrompez votre rcital sous les hues. <br/>";
                            majReputation -= 2;
                        } else {
                            texte += "Mais votre interprtation est peu applaudie. <br/>";
                        }
                    }
                }
                // gloutonnerie
                if (getValeurVice(perso, TypeVice.gourmand) < 2) {
                    if (Math.random() >= 0.9) {
                        texte += ajouterViceVal(perso, TypeVice.gourmand, 1);
                    }
                }


                majReputationDansQuartier(perso, Quartier.chatenay_malabry, majReputation);
                return texte;
            },
            conditions: (perso: Perso): boolean => perso.lieu.quartier === Quartier.chatenay_malabry,
        },
    ],
    probaParDefaut: 10, // >  la moyenne car localiss  un quartier
};