/*
Les talents sont des genres de comptences boolennes rares
Il n'y a pas de raison d'ajouter tous les talents de warhammer, seulement des talents ayant de la personnalit
et qui dclenchent des vnements
-> certaines comptences peu intressante sont transformes en talents comme la natation
 */

import {Perso} from "../types/perso/Perso";

export enum Maitrise {
    beni = "Bni", // bni par un dieu (celui de perso.dieu), permet d'accomplir des miracles
    natation = "Natation",
    poesie = "Poesie",
    cuisine = "Cuisine",
}

export function aLaMaitrise(perso: Perso, maitrise: Maitrise): boolean {
    return perso.maitrises.find((tal: Maitrise) => tal === maitrise) !== undefined;
}

export function ajouterMaitrise(perso: Perso, maitrise: Maitrise): string {
    perso.maitrises.push(maitrise);
    return "Apprentissage de la matrise " + maitrise.toString() + ".";
}
