import {GroupeEvts} from "../../../../types/Evt";
import {Perso} from "../../../../types/perso/Perso";
import {TypeCompetence} from "../../../../types/perso/comps/Comps";
import {ResultatTest} from "../../../../types/LancerDe";
import {testComp} from "../../../../fonctions/des";
import {
    ajouterVertuVal,
    ajouterViceVal,
    getValeurVertu,
    getValeurVice,
    TypeVertu,
    TypeVice
} from "../../../../types/ViceVertu";
import {majReputationDansQuartier} from "../../../../types/Reputation";
import {Quartier} from "../../../geographie/quartiers";
import {ajouterMaitrise, Maitrise} from "../../../maitrise";
import {Coterie} from "../../../../types/Coterie";

export const evts_lycee_celtes: GroupeEvts = {
    evts: [
        {
            id: "evts_lycee_celtes1_intimidation",
            description: (perso: Perso): string => {
                let texte:string = "L'intimidation et la vantardise sont des traditions prcieuses chez les celtes, qui peuvent se renvoyer des insultes des nuits entires pendant leurs banquets ou leurs duels rituels. "
                    + "Il va de soit que vous tes forms, en particulier lors des soires tudiantes. Vous apprenez aussi  vous peindre le visage de manire terrifiante.<br/> ";
                    const resTest:ResultatTest = testComp(perso, {comp: TypeCompetence.intimidation, bonusMalus: 0});
                    texte += resTest.resume;
                    if (resTest.reussi) {
                        texte += "Vous tes trs dou  ce petit jeu. <br/>";
                        // se fait connatre dans le coin
                        texte += majReputationDansQuartier(perso, Quartier.chatenay_malabry, 1);
                    } else {
                        texte += "Vous ne faites clairement pas le poids, mais vous tes au lyce pour apprendre ! <br/>";
                    }
                // gloutonnerie
                if (getValeurVice(perso, TypeVice.orgueilleux) < 2) {
                    if (Math.random() >= 0.9) {
                        texte += ajouterViceVal(perso, TypeVice.orgueilleux, 1);
                    }
                }

                return texte;
            },
            conditions: (perso: Perso): boolean => perso.bilanLycee.coterieActuelle === Coterie.celtes,
        },
        {
            id: "evts_lycee_celtes2_festin",
            description: (perso: Perso): string => {
                let texte:string = "Tout bon celte doit tre un hte de qualit. Savoir discourir, accueillir, cuisiner, servir, sont des comptences qui attirent respect et amiti.<br/> ";
                    const resTest:ResultatTest = testComp(perso, {comp: TypeCompetence.eloquence, bonusMalus: 0});
                    texte += resTest.resume;
                    if (resTest.reussi) {
                        texte += "Vous savez accueillir et divertir. <br/>";
                        // se fait connatre dans le coin
                        texte += majReputationDansQuartier(perso, Quartier.chatenay_malabry, 1);
                    } else {
                        texte += "Vous tes encore maladroit pour un hte mais on pardonne beaucoup  un tudiant. <br/>";
                    }
                // gloutonnerie
                if (getValeurVice(perso, TypeVice.gourmand) < 2) {
                    if (Math.random() >= 0.9) {
                        texte += ajouterViceVal(perso, TypeVice.gourmand, 1);
                    }
                }
                if (Math.random() >= 0.9) {
                    texte += "Vous prenez got  la cuisine et y devenez trs dou. ";
                    texte += ajouterMaitrise(perso, Maitrise.cuisine);
                }

                return texte;
            },
            conditions: (perso: Perso): boolean => perso.bilanLycee.coterieActuelle === Coterie.celtes,
        },
        {
            id: "evts_lycee_celtes3_brimades",
            description: (perso: Perso): string => {
                let texte:string = "Les violences et brimades entre tudiants sont courantes chez les celtes, quand elles ne sont pas encourages. <br/> ";
                    const resTest:ResultatTest = testComp(perso, {comp: TypeCompetence.bagarre, bonusMalus: 0});
                    texte += resTest.resume;
                    if (resTest.reussi) {
                        texte += "Vous savez trs rapidement rendre coup pour coup. Bientt ce sont les autres lves qui ont peur de vous. <br/>";
                        // se fait connatre dans le coin
                        texte += majReputationDansQuartier(perso, Quartier.chatenay_malabry, 1);
                    } else {
                        texte += "Vous tes rgulirement humili, voire tabass par vos camarades. <br/>";
                    }

                if (getValeurVice(perso, TypeVice.colerique) < 2) {
                    if (Math.random() >= 0.9) {
                        texte += ajouterViceVal(perso, TypeVice.colerique, 1);
                    }
                }
                return texte;
            },
            conditions: (perso: Perso): boolean => perso.bilanLycee.coterieActuelle === Coterie.celtes,
        },
        {
            id: "evts_lycee_celtes4_duels",
            description: (perso: Perso): string => {
                let texte:string = "Le combat au corps  corps est une tradition vivace chez les celtes. Le but n'est que rarement de tuer, mais de prouver son habilet et son courage. <br/> ";
                    const resTest:ResultatTest = testComp(perso, {comp: TypeCompetence.armeCaC, bonusMalus: 0});
                    texte += resTest.resume;
                    if (resTest.reussi) {
                        texte += "Vous savez trs rapidement rendre coup pour coup avec courage et habilet. Mme les professeurs commencent  respecter votre force. <br/>";
                        // se fait connatre dans le coin
                        texte += majReputationDansQuartier(perso, Quartier.chatenay_malabry, 1);
                    } else {
                        // TODO : chec critique : blessure
                        texte += "Vous avez beaucoup  apprendre pour avoir le niveau et cesser de vous faire rosser. <br/>";
                    }

                if (getValeurVertu(perso, TypeVertu.valeureux) < 2) {
                    if (Math.random() >= 0.9) {
                        texte += ajouterVertuVal(perso, TypeVertu.valeureux, 1);
                    }
                }
                return texte;
            },
            conditions: (perso: Perso): boolean => perso.bilanLycee.coterieActuelle === Coterie.celtes,
        },
        {
            id: "evts_lycee_celtes5_physique",
            description: (perso: Perso): string => {
                let texte:string = "La forme physique est primordiale pour les celtes. Les entrainements en athltisme sont nombreux et reintants. <br/> ";
                    const resTestMvt:ResultatTest = testComp(perso, {comp: TypeCompetence.mouvement, bonusMalus: 0});
                    texte += resTestMvt.resume;
                    const resTestEnd:ResultatTest = testComp(perso, {comp: TypeCompetence.endurance, bonusMalus: 0});
                    texte += resTestEnd.resume;
                    if (resTestMvt.reussi && resTestEnd.reussi) {
                        texte += "Agile et endurant, vous faites l'admiration de tous. <br/>";
                        // se fait connatre dans le coin
                        texte += majReputationDansQuartier(perso, Quartier.chatenay_malabry, 1);
                    }
                return texte;
            },
            conditions: (perso: Perso): boolean => perso.bilanLycee.coterieActuelle === Coterie.celtes,
        },
        {
            id: "evts_lycee_celtes6_foret",
            description: (perso: Perso): string => {
                let texte:string = "Survivre dans la nature est signe de forte volont et de dbrouillardise pour les celtes, ainsi qu'un lien  conserver avec les anciens dieux. "
                        + "De rudes randonnes et nuits  la belle toile sont donc au programme de leur lyce et vous n'y chapperez pas.<br/> ";
                    const resTestSur:ResultatTest = testComp(perso, {comp: TypeCompetence.survie, bonusMalus: 0});
                    texte += resTestSur.resume;
                    if (resTestSur.reussi) {
                        texte += "Vous semblez tre naturellement chez vous en pleine fort, mme dans la nuit la plus noire,  la grande surprise de vos professeurs. <br/>";
                        // se fait connatre dans le coin
                        texte += majReputationDansQuartier(perso, Quartier.chatenay_malabry, 1);
                    }
                return texte;
            },
            conditions: (perso: Perso): boolean => perso.bilanLycee.coterieActuelle === Coterie.celtes,
        },
        {
            id: "evts_lycee_celtes7_poesie",
            description: (perso: Perso): string => {
                let texte:string = "Votre professeur de diction tente de vous inculquer les bases du discours et de la posie.<br/> ";
                const resTest:ResultatTest = testComp(perso, {comp: TypeCompetence.eloquence, bonusMalus: -10});
                texte += resTest.resume;
                if (resTest.reussi) {
                    texte += "Vous avez un talent de pote inn qui impressionne fortement votre professeur. <br/>";
                    texte += ajouterMaitrise(perso, Maitrise.poesie);
                    // se fait connatre dans le coin
                    texte += majReputationDansQuartier(perso, Quartier.chatenay_malabry, 1);
                }

                return texte;
            },
            conditions: (perso: Perso): boolean => perso.bilanLycee.coterieActuelle === Coterie.celtes,
        },
    ],
    probaParDefaut: 40, // >>>  la moyenne car localiss  un quartier et une phase
};